@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
html {
  margin: 0;
  padding: 0;
}

* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#addprt {
  padding: 1rem;
  background-color: lawngreen;
}

#stud {
  text-align: center;
  margin: 0;
}

.break {
  clear: both;
}
.Toolbar {
    height: 56px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgb(86, 25, 148);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
    z-index: 100;
    color: white;
}

.Toolbar nav {
    height: 100%;
}

#Toolbar-logo {
    height: 50px;
}
#NavItems {
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    list-style: none;
    height: 100%;
    box-sizing: border-box;
}

.NavItem {
    margin: 0;
    box-sizing: border-box;
    display: flex;
    height: 100%;
    align-items: center;
}

.NavItem a {
    color: white;
    height: 100%;
    text-decoration: none;
    border-bottom: 2px solid transparent;
    padding: 1rem 10px;
    box-sizing: border-box;
    display: block;
}

.NavItem a:hover,
.NavItem a:active {
    background-color: coral;
    border-bottom: 2px solid orangered;
    color: rebeccapurple;
}
#intro-flex {
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
}

#intro-svg {
    margin-top: 3rem;
    padding-top: 1.5rem;
}

#intro-text {
    padding-top: 40vh;
    padding-right: 20vw;
}

#intro-logo {
    height: 10rem;
}

@media(max-width: 800px) { 
    svg {
        height: 0;
        width: 0;
    }

    #intro-text {
        padding-top: 30vh;
        padding-right: 0;
    }

    #intro-flex {
        display: contents;
        text-align: center;
    }
}
#Subjects-ul {
    list-style: none;
    background-color: white;
    margin: 2rem;
    padding: 1rem;
}

.Subjects-li {
    display: flex;
    box-sizing: border-box;
    margin: 10px;
}

#Part-p {
    padding: 10px;
    margin: 10px;
    border-radius: 200px;
    background-color: rebeccapurple;
    color: white;
}

#Part-p:focus {
    outline-style: none;
}

#Part-p:active {
    background-color: tomato;
}

@media (max-width: 800px) {
    #Subjects-ul {
        margin: 10px;
        margin-bottom: 2rem;
    }
}

@media (max-width: 600px) {
    #Part-p {
        margin: 0;
    }

    #Subjects-ul {
        margin: 5px;
        padding: 0;
    }

    .Subjects-li {
        margin: 10px 5px;
    }

}
#student {
    text-align: center;
    width: 60%;
    margin: 1rem auto;
    border: 1px solid black;
    box-sizing: border-box;
}

.Students-ul {
    background-color: powderblue;
    display: flex;
    justify-content: space-between;
    list-style: none;
    padding: 1rem;
}

.Stud-li {
    padding: 0.5rem;
}

#dlt {
    background-color: red;
    padding: 0.5rem;
    margin: auto;
}

@media (max-width: 900px) {
    #student {
        width: 100%;
    }
}
footer {
    text-align: center;
    background-color: rgb(172, 41, 67);
    color: white;
}

#footer-logo {
    margin: 2rem;
}

#footer-p {
    padding-bottom: 2rem;
}
#intro-page {
    background-color: #ff7f50;
    height: 105vh;
}

#subject-page {
    padding-bottom: 5rem;
    background-color: rebeccapurple;
}

.subject-page-h2 {
    padding-top: 5rem;
    padding-bottom: 2rem;
    text-align: center;
    color: white;
    font-weight: 700;
}

.subject-page-list {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

#select-sub {
    text-align: center;
    color: white;
    padding-top: 1rem;
}

#stud {
    background-color: #f35217;
}

#show-part {
    text-align: center;
}

.view-p {
    padding: 1rem;
    background-color: crimson;
    color: white;
}

.view-p:focus {
    outline-style: none;
}

.view-p:active {
    background-color: orangered;
}

.stud-h2 {
    padding-top: 2rem;
    color: wheat;
}

.stud-final-btn {
    padding: 0.5rem;
    margin: 0.5rem;
    margin-bottom: 3rem;
    background-color: crimson;
}

.stud-final-btn:focus {
    outline: none;
}

.stud-final-btn:focus {
    background-color: lawngreen;
}
