.Toolbar {
    height: 56px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgb(86, 25, 148);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
    z-index: 100;
    color: white;
}

.Toolbar nav {
    height: 100%;
}

#Toolbar-logo {
    height: 50px;
}